import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Col, Card, Form, message } from 'antd';
import { TableData, DatePickerString } from 'components/common';
import { getPaperStudentById, updateStudentPaper } from 'services/paperStudent';
import moment from 'moment';
import { dateToString } from 'utils/formatUtil';

@Form.create({ name: 'FormReturnDoc' })
class FormReturnDoc extends Component {
  state = {
    reload: 0,
    paramSearch: {},
    paperSelected: [],
    paperSelectedRowKeys: [],
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      const paramSearch = {};
      paramSearch.studentId = match.params.id;
      this.setState({ paramSearch });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, { sentDate }) => {
      if (!err) {
        const promises = this.state.paperSelected.map(paperSelected =>
          this.handleUpdate(sentDate, paperSelected)
        );
        try {
          await Promise.all(promises);
          // reload table
          this.setState({
            reload: this.state.reload + 1,
          });
          message.success('Cập nhật thành công');
        } catch (ex) {
          message.error(ex.message);
        } finally {
          this.setState({
            paperSelected: [],
            paperSelectedRowKeys: [],
          });
        }
      }
    });
  };

  handleUpdate = async (sentDateStr, paperSelected) => {
    const data = {
      ...paperSelected,
      returnedDate: sentDateStr,
    };
    await updateStudentPaper(data);
  };

  columns = [
    {
      title: 'Các loại giấy tờ sinh viên nộp',
      dataIndex: 'paperTypeLabel',
    },
    {
      title: 'Ngày trả',
      dataIndex: 'returnedDate',
      render: text => (text ? dateToString(text) : ''),
    },
  ];

  render() {
    const { getFieldDecorator } = this.props.form;
    const { paperSelectedRowKeys, paperSelected, reload, paramSearch } = this.state;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };

    const rowSelection = {
      selectedRowKeys: paperSelectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState({
          paperSelected: selectedRows,
          paperSelectedRowKeys: selectedRowKeys,
        });
      },
      getCheckboxProps: record => ({
        disabled: !!record.returnedDate,
        name: record.returnedDate,
      }),
    };

    return (
      <Card>
        <Row>
          <Form onSubmit={this.handleSubmit}>
            <Row gutter={24}>
              <Col span={8}>
                <Form.Item {...formItemLayout} label="Ngày trả">
                  {getFieldDecorator('sentDate', {
                    initialValue: moment(),
                  })(<DatePickerString />)}
                </Form.Item>
              </Col>
              <Col span={16}>
                <BtnSave
                  htmlType="submit"
                  disabled={paperSelected.length < 1}
                  title="Trả giấy tờ"
                />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Col>
            </Row>
            <TableData
              rowSelection={rowSelection}
              reload={reload}
              paramSearch={paramSearch}
              service={getPaperStudentById}
              allowGetData={!!paramSearch.studentId}
              columns={this.columns}
              rowKey="id"
            />
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormReturnDoc {...props} />
  </Page>
));



